body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
blockquote,
dl,
dt,
dd,
ul,
ol,
li,
pre,
form,
fieldset,
legend,
button,
input,
textarea,
th,
td {
	margin: 0;
	padding: 0;
	border: 0;
}
html {
	color: #000;
}
html,
body {
	height: 100%;
}
body,
button,
input,
select,
textarea {
	font-size: 12px;
	font-family: 'Microsoft Yahei', '\5FAE\8F6F\96C5\9ED1', \5FAE\8F6F\96C5\9ED1, '\5B8B\4F53', \5b8b\4f53, arial, 'Hiragino Sans GB', Tahoma, Arial, Helvetica;
	background: transparent;
}
ul,
ol,
li,
dl,
dd,
dt {
	list-style: none;
}
em {
	font-style: normal;
}
a {
	text-decoration: none;
}
a:hover {
	text-decoration: underline;
}
legend {
	color: #000;
}
fieldset,
img {
	border: 0;
}
label,
button {
	cursor: pointer;
}

/*解决IE图片缩放失真*/
img {
	-ms-interpolation-mode: bicubic;
}

/*清除浮动*/
.clearfix:after {
	content: '';
	display: block;
	height: 0;
	clear: both;
}
.clearfix {
	zoom: 1;
}
.clear {
	clear: both;
}

/*连续英文、数字换行*/
.wordwrap {
	word-break: break-all;
	word-wrap: break-word;
}

/*单行文字超出显示省略号*/
.FWD_ellipsis {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

/*所有按钮、checkbox、radio显示pointer*/
input,
button,
input[type="radio"],
input[type="checkbox"],
textarea {
	background-color: transparent;
	cursor: pointer;
	resize: none;
}
input:focus,button:focus,input[type="checkbox"]:focus,input[type="radio"]:focus,textarea:focus{
	outline: none;
}

/*按钮初始样式*/
.btn_normal {
	width: 120px;
	height: 40px;
	color: #fff;
	line-height: 40px;
	text-align: center;
	font-size: 14px;
	border: none;
	border-radius: 5px;
	background: #f1f1f1;
}

/*按钮鼠标划上的效果*/
button:hover {
	opacity: 0.8;
}
.btn_normal.btn_default{
	background: #ccc;
}
.btn_normal.btn_default:active{
	background: #c3c3c3;
}

/*按钮成功状态*/
.btn_normal.btn_success {
	background:#46be8a ;
}

/*成功按钮点击的效果*/
.btn_normal.btn_success:active {
	background:#00bf6c ;
}

/*按钮错误状态*/
.btn_normal.btn_error {
	background:#ff7272 ;
}
.btn_normal.btn_error:active{
	background: #ff3939;
}

/*按钮警告状态*/
.btn_normal.btn_warming {
	background: #f99800;
}
.btn_normal.btn_warming:active{
	background: #f9ae00;
}
.btn_normal.btn_win{
	background:#0093e7 ;
}
.btn_normal.btn_win:active{
	background: #00a5f7;
}

/*按钮禁用状态*/
.btn_normal:disabled {
	background: #ccc;
	cursor: not-allowed;
}

/*input获得焦点的效果*/
input{
	border: 1px solid #f1f1f1;
}
input:focus{
	-webkit-box-shadow: 0px 0px 5px #c1c1c1;
	        box-shadow: 0px 0px 5px #c1c1c1;
}
